export default {
    data() {
        return {
            documentType:['身份证','护照','台胞证','港澳通行证','军官证'], show: false, disabled: false, isBind: false,
            bindList:{ phone:'', buyerName:'',cardNo:'', cardType: '' }, cardTypeName: '', success: false, is: true
        }
    },
    components: {
        
    },
    computed: {
        user(){
            if(this.$store.state.user == '') this.$store.commit('USER')
            return this.$store.state.user
        },
    },
    created(){
        this.bindList.phone = this.$route.query.phone || ''
    },
    mounted(){
        
    },
    methods: {
        isShow(){
            this.show = !this.show
            if(this.is){
                this.cardTypeName = this.documentType[0]
                this.bindList.cardType = 0
                this.is = false
            }
        },
        onConfirm(value, index) {
            this.cardTypeName = value
            this.bindList.cardType = index
            this.show = !this.show
        },
        onCancel() {
            this.show = !this.show
        },
        onChange(picker, value, index) {
            this.cardTypeName = value
            this.bindList.cardType = index
        },
        onRead(file){
            this.bindList.files.push(file)
        },
        isChangePhone(){
            this.$store.commit('isChangePhone', true)
            this.$router.push('/Authentication')
        },
        next(){
            if(this.isBind){
                this.success = true
                setTimeout(()=>{this.success = false},3000)
                this.user.phonenumber != '' ? this.bindList.phone = this.user.phonenumber : ''
                this.$store.dispatch('auth', this.bindList)
            }
        }
    },
    watch: {
        bindList:{
            handler(val,old) {
                val.buyerName != '' && val.cardType >= 0 && val.cardNo.length > 0 ? this.isBind = true : this.isBind = false
            },
            deep: true
        }
    }
}



